.body {
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
}

.title {
  position: absolute;
  z-index: 2;
  top: 0%;
  left: 50%;
  transform: translate(-50%);
  padding: 15px 30px;
  font-size: 30px;
  font-weight: 600;
}

.goBackBtn {
  position: absolute;
  z-index: 2;
  right: 1%;
  top: 1%;
  padding: 5px 20px;
  margin-top: 5px;
  text-decoration: none;
  color: #000;
  border: 1px solid #ccc;
  border-radius: 10px;
}

.goBackBtn:hover {
  transition: 0.3s;
  border: 1px solid #000;
}

.flex1 {
  flex: 1;
  min-width: 700px;
  overflow: hidden;
  background-color: #fff;
  position: relative;
}

.h1 {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  background-color: #fff;
  padding: 10px;
}

.body img {
  object-fit: cover;
  margin-top: 10vh;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 80vh;
  transition: 0.5s;
}

.body img:hover {
  transform: scale(1.1);
  transition: 0.5s;
  cursor: pointer;
  opacity: 0.8;
}

.name {
  z-index: 3;
  color: #000;
  position: absolute;
  bottom: 3%;
  left: 50%;
  transform: translate(-50%);
  font-size: 25px;
  overflow: hidden;
}

@media (max-width: 575.98px) {
  .body {
    overflow: hidden;
  }
  .body img {
    width: 60%;
    height: 380px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 7vh;
  }

  .name {
    font-size: 0;
  }
}

@media (max-width: 767.98px) {
  .title {
    font-size: 0;
  }
}
